.header {
	font-family: 'TT-travels';
	font-weight: 600;
    font-style: normal;
	font-size: 24px;
	color: white;
	padding: 8vh;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.block {
	min-height: 100vh;
}
.block-header {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	margin-top: 10vh;
	margin-left: 10vh;
	color: white;
}

#main-block {
	background-image: url('./images/bg-main.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

#main-block > h1 {
	font-family: "TT-travels";
	font-weight: 500;
    font-style: italic;
	color: white;
	text-align: center;
	font-size: 38px;
	margin: 15vh;
}

#about-block > h2 {
	font-family: 'TT-travels';
	font-weight: 500;
    font-style: normal;
	margin-top: 10vh;
	margin-left: 10vh;
	color: white;
	width: 100vh;
	font-size: 21px;
}
.numbers {
	display: flex;
	justify-content: center;
	align-items: center;
	padding-top: 10vh;
}
.number-item {
	display: flex;
	padding-bottom: 10vh;
}
.number-item > h1 {
	font-family: 'TT-travels';
	font-weight: 500;
    font-style: normal;
	font-size: 86px;
	margin: 0px;
	color: white;
}
.number-item > div {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	font-size: 14px;
	margin: 0px 20px;
	color: white;
	display: flex;
	align-items: center;
	width: 250px;
}

.services-list {
	list-style-type: none;
	padding-right: 50px;
	border-right: thick solid #ffffff;
	border-width: 1px;
	display: inline-block;
}
.list-item {
	font-family: 'TT-travels';
	font-weight: 300;
    font-style: normal;
	font-size: 18px;
	color: white;
	padding: 10px;
	background-color: transparent;
	cursor: pointer;
	transition: font-weight 250ms ease;
}
.list-item:hover {
	font-weight: 500;
}
.list-item:active {
	font-weight: 300;
}